</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  height: 300px;
  margin-top: 100px;
  font-size: 64px;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



